@import url(https://fonts.googleapis.com/css2?family=Raleway:wght@200;500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Cardo:ital@1&display=swap);
.main {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    color: #fff;
    height: 100vh;
    background: url(/static/media/background.3762394e.jpg) no-repeat center;
    background-size: cover;
}

.container {
    margin: 0 auto;
    width: 1170px;
}

.header {
    display: flex;
    justify-content: space-between;
    height: 100px;
    align-items: center;
}

.menu {
    width: 600px;
}

.menu ul{
    display: flex;
    justify-content: space-between;
    list-style: none;
}

.menuLink {
    text-decoration: none;
    color: #fff;
    font-family: 'Raleway', sans-serif;
    text-transform: uppercase;
    font-weight: bold;
}

.menuLink:hover {
    color: #b4b4b4
}

.line {
    width: 100%;
    height: 1px;
    background-color: #fff;
    opacity: 0.1;
}

.main-block {
    text-align: center;
    max-width: 920px;
    margin: auto;
}

.main-block h1 {
    margin-top: 100px;
    margin-bottom: 40px;
    text-transform: uppercase;
    color:#FFFFFF;
    font-family: 'Raleway', sans-serif;
    font-size: 65px;
}

.main-block p {
    max-width: 920px;
    font-family: 'Raleway', sans-serif;
    font-size: 22px;
    line-height: 1.4;
}

.mainBtn {
    background-color:#E84545;
    color: #fff;
    padding: 15px 55px;
    border: none;
    text-transform: uppercase;
    font-family: 'Raleway', sans-serif;
    font-size: 14px;
    margin-top: 50px;
    margin-bottom: 75px;
    cursor: pointer;
}

.mainBtn:hover {
    background-color:#942121;
}
.blocks {
    display: flex;
    justify-content: space-around;   
    flex-wrap: wrap;
    margin-top: 50px;
    margin-bottom: 100px;
}

.block {
    text-align: center;
    width: 300px;
    margin-top: 50px;
} 

.blockHeader {
    font-family: 'Raleway', sans-serif;
    font-size: 14px;
    font-weight: bold;
    text-transform: uppercase;
    margin-top: 30px;
    margin-bottom: 20px;
}

.blockText {
    font-family: 'Raleway', sans-serif;
    font-size: 14px;
    line-height: 1.5;
}

.works {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    margin-bottom: 50px;
}


.work {
    position: relative;
    width: 337px;
    height: 235px;
}

.workImg {
    width: 337px;
    height: 235px;
}

.cursor {
    width: 337px;
    height: 235px;
    z-index: 99;
    position: absolute;
    top: 0;
    left:0;
    background-color:rgba(250, 34, 34, 0.7);
    padding:20px;
    display: none;
    text-align: center;
}

.work:hover .cursor {
    display: block;
}

.cursorHeader {
    padding-top: 50px;
    color: #fff;
    font-family: 'Raleway', sans-serif;
    font-size: 18px;
    font-weight: bold;
    text-transform: uppercase;
}

.cursorText {
    color: #fff;
    font-family: 'Raleway', sans-serif;
}
